import { BehaviorSubject, take } from "rxjs";

import { type IDeviceAndBrowserSpecs } from "./types";

import {
  getAudioFingerprint,
  getFonts,
  getPlugins,
  getCanvasFingerprint,
  getTouchSupport,
  getOsCpu,
  getLanguages,
  getColorDepth,
  getDeviceMemory,
  getScreenResolution,
  getRoundedScreenFrame,
  getHardwareConcurrency,
  getTimezone,
  getSessionStorage,
  getLocalStorage,
  getIndexedDB,
  getOpenDatabase,
  getPlatform,
  getVendor,
  getVendorFlavors,
  areCookiesEnabled,
  getDomBlockers,
  getColorGamut,
  areColorsInverted,
  areColorsForced,
  getMonochromeDepth,
  getContrastPreference,
  isMotionReduced,
  isHDR,
  getMathFingerprint,
  getFontPreferences,
  getVideoCard,
  isPdfViewerEnabled,
  getArchitecture,
} from "./fingerprintjs";
import {
  getBattery,
  getBluetoothAvailability,
  getPixelDepth,
  getServiceWorker,
  storageEstimate,
} from "./custom";

class DeviceAndBrowserSpecs {
  readonly $specs = new BehaviorSubject<null | IDeviceAndBrowserSpecs>(null);

  generateSpecs(): void {
    const audioFingerprint = getAudioFingerprint();

    Promise.all([
      getFonts(),
      getDomBlockers(),
      getFontPreferences(),
      typeof audioFingerprint === "number"
        ? audioFingerprint
        : audioFingerprint(),
      getRoundedScreenFrame()(),
      getBattery(),
      getBluetoothAvailability(),
      storageEstimate(),
    ]).then(
      ([
        fonts,
        domBlockers,
        fontPreferences,
        audio,
        screenFrame,
        battery,
        bluetoothAvailable,
        storageEstimate,
      ]) => {
        const specs: IDeviceAndBrowserSpecs = {
          stable: {
            // FingerprintJS
            fonts,
            domBlockers,
            fontPreferences,
            audio,
            screenFrame,
            osCpu: getOsCpu(),
            languages: getLanguages(),
            colorDepth: getColorDepth(),
            deviceMemory: getDeviceMemory(),
            screenResolution: getScreenResolution(),
            hardwareConcurrency: getHardwareConcurrency(),
            timezone: getTimezone(),
            sessionStorage: getSessionStorage(),
            localStorage: getLocalStorage(),
            indexedDB: getIndexedDB(),
            openDatabase: getOpenDatabase(),
            platform: getPlatform(),
            plugins: getPlugins(),
            canvas: getCanvasFingerprint(),
            touchSupport: getTouchSupport(),
            vendor: getVendor(),
            vendorFlavors: getVendorFlavors(),
            cookiesEnabled: areCookiesEnabled(),
            colorGamut: getColorGamut(),
            invertedColors: areColorsInverted(),
            forcedColors: areColorsForced(),
            monochrome: getMonochromeDepth(),
            contrast: getContrastPreference(),
            reducedMotion: isMotionReduced(),
            hdr: isHDR(),
            math: getMathFingerprint(),
            videoCard: getVideoCard(),
            pdfViewerEnabled: isPdfViewerEnabled(),
            architecture: getArchitecture(),
            // (END) FingerprintJS
            serviceWorker: getServiceWorker(),
            pixelDepth: getPixelDepth(),
            bluetoothAvailable,
          },
          unstable: {
            userAgent: navigator.userAgent,
            userAgentData: navigator.userAgentData,
            performance,
            connection: {
              downlink: navigator.connection?.downlink,
              effectiveType: navigator.connection?.effectiveType,
              rtt: navigator.connection?.rtt,
              saveData: navigator.connection?.saveData,
            },
            screen: {
              orientation: {
                angle: screen.orientation.angle,
                type: screen.orientation.type,
              },
            },
            innerWidth,
            innerHeight,
            battery: {
              charging: battery.charging,
              chargingTime: battery.chargingTime,
              dischargingTime: battery.dischargingTime,
              level: battery.level,
            },
            storageEstimate,
          },
        };

        // this.printObject(specs)
        this.$specs.next(specs);
      }
    );
  }

  // private printObject(object: object): void {
  //   console.log(
  //     JSON.stringify(object, (key, value) => {
  //       if (key) JSON.stringify(key)

  //       return value === undefined ? null : value
  //     })
  //   )
  // }
}

export const deviceAndBrowserSpecs = new DeviceAndBrowserSpecs();
