import {
  BehaviorSubject,
  firstValueFrom,
  take,
  tap,
  type Observable,
  of,
} from "rxjs";

// import { http } from 'src/common/http'
// import { environment } from 'src/environments/environment'
// import { type OnInit, type OnDestroy } from 'src/common'

class Time {
  private readonly initialBrowserTimestamp = new BehaviorSubject<null | number>(
    null
  );
  private readonly initialServerTimestamp = new BehaviorSubject<null | number>(
    null
  );
  private readonly serverAndBrowserTimestampDifference = new BehaviorSubject<
    null | number
  >(null);
  get currentTimestampMS(): number {
    return (
      new Date().getTime() +
      (this.serverAndBrowserTimestampDifference.value ?? 0)
    );
  }

  async onInit(): Promise<void> {
    await this.initTimestamps();
  }

  onDestroy(): void {
    this.initialBrowserTimestamp.next(null);
    this.initialServerTimestamp.next(null);
    this.serverAndBrowserTimestampDifference.next(null);
  }

  private async initTimestamps(): Promise<void> {
    await firstValueFrom(
      this.getServerCurrentTimestamp().pipe(
        take(1),
        tap((timestamp) => {
          this.initialServerTimestamp.next(timestamp);
          this.initialBrowserTimestamp.next(new Date().getTime());

          this.serverAndBrowserTimestampDifference.next(
            (this.initialServerTimestamp.value ?? 0) -
              (this.initialBrowserTimestamp.value ?? 0)
          );
        })
      )
    );
  }

  private getServerCurrentTimestamp(): Observable<number> {
    return of(new Date().getTime());
  }
}

export const time = new Time();
